


export const SupportedLanguages = {
    fr: 'fr',
    en: 'en',
    sw: 'sw',
}

export default class Internationalisation {
    currentLanguage = {};

    languages = [
        {
            'code': 'fr', 'flag': '',
            'fr': 'Franais',
            'en': 'French',
            'sw': "Kifaransa",
        },
        {
            'code': 'en', 'flag': '',
            'fr': 'Anglais',
            'en': 'English',
            'sw': "Kingereza",
        },
        {
            'code': 'sw', 'flag': '',
            'fr': 'Swahili',
            'en': 'Swahili',
            'sw': "Kiswahili",
        },

    ];

    getLanguageByCode(code) {
        switch (code) {
            case 'fr': return this._french();
            case 'en': return this._english();
            case 'sw': return this._swahili();
            default: return this._french();
        }
    }

    _french() {
        return { 'code': 'fr' };
    }
    _english() {
        return { 'code': 'en' };
    }
    _swahili() {
        return { 'code': 'sw' };
    }

    codeToType(code){
        switch (code) {
            case 'fr':
                return SupportedLanguages.fr;
            case 'sw':
                return SupportedLanguages.sw;
            case 'en':
                return SupportedLanguages.en;
            default:
                return SupportedLanguages.fr;
        }
    }

    typeToCode(language){
        switch (language) {
            case SupportedLanguages.fr:
                return 'fr';
            case SupportedLanguages.en:
                return 'en';
            case SupportedLanguages.sw:
                return 'sw';
            default:
                return 'fr';
        }
    }

    onLanguageChanged(language) {
        switch (language) {
            case SupportedLanguages.fr:
                this.currentLanguage = this._french();
                break;
            case SupportedLanguages.en:
                this.currentLanguage = this._english();
                break;
            case SupportedLanguages.sw:
                this.currentLanguage = this._swahili();
                break;
            default:
                this.currentLanguage = this._french();
                break;
        }
    }
}