import React, { Component } from 'react'
import { amenities, isEmpty, getErrorMessage, capitalize } from '../../../utils/utils';
import LocationPickerInput from '../location_picker/location_picker';
import MultiSelectInput from '../multi_select_input/multi_select_input';
import Notifier from '../notifier/notifier';
import UpdateBedrooms from './update_bedrooms';
import { connect } from "react-redux";
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import { appName, housesImagesS3FolderNAme, systemHousesBaseUrl } from "../../../config/config";
import Joi from "joi";
import RentalForm from './rental_form';
import SaleForm from './sale_form';
import DetailsModal from '../../pages/details_modal/details_modal';
import DetailsDisplayer from '../details_displayer/details_displayer';
import HouseDetails from './house_details';
import Caroussel002 from '../caroussel/caroussel002';
import Loader from '../loader/loader';
import DeleteEntity from '../delete_enity_confirm/delete_entity';
import { copyWith } from '../../../logic/slice';


class UpdateHouse extends Component {

    getFormValues(building) {
        const {
            BuildingTypeId,
            Latitude,
            Longitude,
            Address,
            LivingRoom,
            DinningRoom,
            BathRoom,
            Parking,
            Garage,
            Kitchen,
            Office,
            Balcony,
            Size,
            BuiltIn,
            Sauna,
            Wifi,
            Laundry,
            Garden,
            Gym,
            SwimmingPool,
            Heater,
            Jacuzy,
            StoreRoom,
            AirConditioner,
            PetFriendly,
            Funished,
            Doorbell,
            BackupSourceForPowerOutage,
            Description,
        } = building;
        return {
            BuildingTypeId: BuildingTypeId ?? 0,
            Latitude: Latitude,
            Longitude: Longitude,
            Address: Address,
            LivingRoom: LivingRoom,
            DinningRoom: DinningRoom,
            BathRoom: BathRoom,
            Parking: Parking,
            Garage: Garage,
            Kitchen: Kitchen,
            Office: Office,
            Balcony: Balcony,
            Size: Size,
            BuiltIn: BuiltIn,
            Sauna: Sauna,
            Wifi: Wifi,
            Laundry: Laundry,
            Garden: Garden,
            Gym: Gym,
            SwimmingPool: SwimmingPool,
            Heater: Heater,
            Jacuzy: Jacuzy,
            StoreRoom: StoreRoom,
            AirConditioner: AirConditioner,
            PetFriendly: PetFriendly,
            Funished: Funished,
            Doorbell: Doorbell,
            BackupSourceForPowerOutage: BackupSourceForPowerOutage,
            Description: Description,
        }
    }

    componentDidMount() {
        if (!this.props.propertyTypes.length || !this.props.RentalTypes.length) {
            this.utilsFetcher();
        }
        this.GetEntityObject();
    }

    state = {
        errorMessage: '',
        errorObjectMessage: '',
        successMessage: '',
        formValues: {},
        formValuesToSend: {},
        isDisabled: true,
        isAsyncCall: false,
        isAsyncObjectCall: true,
        isForSale: false,
        isForRent: false,
        isVisible: false,
        isDetailsDisplayed: false,
        building: {},
        wantToDeleteAccount: false,
    }

    getSelectedAmenities(formValues) {
        const keys = Object.keys(formValues);
        const selectedAmenities = [];
        for (var index = 0; index < amenities.length; index++) {
            // eslint-disable-next-line no-loop-func
            if (keys.filter((key) => key === amenities[index].key).length) {
                if (formValues[amenities[index].key]) {
                    selectedAmenities.push(amenities[index])
                }
            }
        }
        return selectedAmenities;
    }

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState(state => ({ ...state, formValues: { ...state.formValues, [name]: value }, formValuesToSend: { ...state.formValuesToSend, [name]: value } }));
    }

    joiSchema = Joi.object({
        BuildingTypeId: Joi.number().min(0),
        Latitude: Joi.number().not(0.0),
        Longitude: Joi.number().not(0.0),
        Address: Joi.string(),
        LivingRoom: Joi.number().min(0),
        DinningRoom: Joi.number().min(0),
        BathRoom: Joi.number().min(0),
        Parking: Joi.number().min(0),
        Garage: Joi.number().min(0),
        Kitchen: Joi.number().min(0),
        Office: Joi.number().min(0),
        Balcony: Joi.number().min(0),
        Size: Joi.number().min(0),
        BuiltIn: Joi.number().min(0),
        Sauna: Joi.boolean(),
        Wifi: Joi.boolean(),
        Laundry: Joi.boolean(),
        Garden: Joi.boolean(),
        Gym: Joi.boolean(),
        SwimmingPool: Joi.boolean(),
        Heater: Joi.boolean(),
        Jacuzy: Joi.boolean(),
        StoreRoom: Joi.boolean(),
        AirConditioner: Joi.boolean(),
        PetFriendly: Joi.boolean(),
        Funished: Joi.boolean(),
        Doorbell: Joi.boolean(),
        BackupSourceForPowerOutage: Joi.boolean(),
        Description: Joi.string(),
    });

    render() {
        const isDisabled = this.state.isDisabled;
        const startYear = new Date().getFullYear() - 100;
        return (this.state.isAsyncObjectCall || !isEmpty(this.state.errorObjectMessage)) ? <div className="details_displayer_screen_message_loader">
            {this.state.isAsyncObjectCall && <Loader />}
            <div className="divider" /><div className="divider" />
            {!isEmpty(this.state.errorObjectMessage) && <>
                <Notifier type='error' message={this.state.errorObjectMessage} />
                <div className="divider" />
            </>}
        </div> : (!this.state.isAsyncObjectCall && isEmpty(this.state.errorObjectMessage)) && <div className='update_houses_screen'>
            {this.state.isDetailsDisplayed && <DetailsModal
                body={<DetailsDisplayer detailsOptions={{
                    entityId: this.state.building.id,
                    images: this.state.building.Images,
                    baseUrl: systemHousesBaseUrl,
                    entityEndpoint: 'building/my-own-building-detail',
                    likeEndpoint: 'like',
                    unlikeEndpoint: 'unlike',
                    commentBody: { BuildingId: this.state.building.id },
                    entityDetailsUi: <HouseDetails building={this.state.building} />,
                    commentEndpoint: 'comment/add-comment',
                    bookmarkEndpoint: 'building',
                    deleteCommentEndpoint: 'comment/remove-comment',
                    fetchingCommentsEnpoint: 'comment/filter-comment',
                    reportEndpoint: 'add-report-house',
                    reportKey: 'idhouse',
                    onObjectReady: (object) => { this.setState({ building: object }) },
                }} />}
                onClose={() => { this.setState({ isDetailsDisplayed: !this.state.isDetailsDisplayed }) }} />
            }
            {this.state.wantToDeleteAccount === true &&
                <DeleteEntity
                    images={this.state.building.Images}
                    video={this.state.building.Video}
                    onClosePressed={() => { this.setState({ wantToDeleteAccount: !this.state.wantToDeleteAccount }) }}
                    onDeleteFinished={() => {
                        this.props.onDeleteObject();
                    }}
                    onDelete={async () => {
                        this.setState({ isAsyncCall: true })
                        const restApiServices = new ApiProvider();
                        return await restApiServices.provider({
                            baseUrl: systemHousesBaseUrl,
                            endpoint: `api/building/remove-building-entity/${this.state.building.id}`,
                            apiMethod: ApiMethods.delete,
                            successCodeExpectation: 200,
                            requestData: {
                                token: {
                                    accessToken: this.props.userInformation?.accessToken
                                }
                            },
                        }).then((response) => {
                            if (typeof (response.error) === "undefined") {
                                this.setState({ isAsyncCall: false });
                                return true;
                            } else {
                                this.setState({ isAsyncCall: false })
                                return false;
                            }
                        });
                    }}
                />
            }
            <div className='update_houses_screen_left'>
                <div className="update_building">
                    <div className="divider" />
                    <div className='update_actions'>
                        <i className='fa fa-rotate-right' onClick={(event) => {
                            event.stopPropagation();
                            this.setState({ errorMessage: '', successMessage: '', infoMessage: '' });
                            this.GetEntityObject();
                        }} />
                        <i className='fa fa-eye' onClick={(event) => {
                            event.stopPropagation();
                            this.setState({ isDetailsDisplayed: !this.state.isDetailsDisplayed });
                        }} />
                        {isDisabled && <i className='fa fa-lock' onClick={(event) => {
                            event.stopPropagation();
                            this.setState({ isDisabled: !isDisabled });

                        }} />}
                        {!isDisabled && <i id='spinner_loader' className='fa fa-unlock' onClick={(event) => {
                            event.stopPropagation();
                            if (Object.keys(this.state.formValuesToSend).length) {
                                this.setState({ errorMessage: '', successMessage: '', infoMessage: '' });
                                this.joiSchema.validateAsync(this.state.formValues).then((formValues) => {
                                    this.setState({ isDisabled: !isDisabled });
                                    this.onUpdate(this.state.formValuesToSend);
                                }).catch((error) => {
                                    this.setState({ errorMessage: `${error.details.map((e) => e.message)}` });
                                });
                            } else {
                                this.setState({ isDisabled: !isDisabled });
                            }
                        }} />}
                        <i className='fa fa-trash' onClick={(event) => {
                            event.stopPropagation();
                            this.setState({ wantToDeleteAccount: !this.state.wantToDeleteAccount });
                        }} />
                        {this.state.isAsyncCall && <i id='spinner_loader' className='fa-solid fa-spinner fa-spin' />}
                    </div>
                    {!isEmpty(this.state.errorMessage) && <>
                        <Notifier type='error' message={this.state.errorMessage} />
                        <div className="divider" />
                    </>}
                    {!isEmpty(this.state.successMessage) && <>
                        <Notifier type='success' message={this.state.successMessage} />
                        <div className="divider" />
                    </>}
                    <div className="update_building_form">
                        <fieldset disabled={isDisabled || this.state.isAsyncCall}>
                            <form onChange={this.handleChange}>
                                <div className="update_input">
                                    <h5>{{
                                        fr: "Type",
                                        en: "Type",
                                        sw: "Aina"
                                    }[`${this.props.currentLanguage}`]}</h5>
                                    <select className="account_type" name="BuildingTypeId" value={Number(this.state.formValues.BuildingTypeId)} onChange={(event) => { }}>
                                        <option value={0} disabled={true}>Type</option>
                                        {this.props.propertyTypes.map((e, i) => {
                                            return <option key={i} value={e.id}>{e[capitalize(this.props.currentLanguage)]}</option>
                                        })}
                                    </select>
                                </div>
                                <div className="update_input">
                                    <h5>{{
                                        fr: "Adresse",
                                        en: "Address",
                                        sw: "Anwani"
                                    }[`${this.props.currentLanguage}`]}</h5>
                                    <input name="Address" onChange={(event) => { }} value={this.state.formValues.Address} />
                                </div>
                                <div className="update_input">
                                    <h5>{{
                                        fr: "Coordonnes",
                                        en: "Cordinates",
                                        sw: "Kuratibu"
                                    }[`${this.props.currentLanguage}`]}</h5>
                                    <LocationPickerInput key={Math.random() * 28937923} isDisabled={isDisabled} intialValue={{ lat: this.state.formValues.Latitude, lng: this.state.formValues.Longitude }} onChange={(lat, lng) => {
                                        this.setState(state => ({ ...state, formValues: { ...state.formValues, Latitude: lat, Longitude: lng }, formValuesToSend: { ...state.formValuesToSend, Latitude: lat, Longitude: lng } }));
                                    }} />
                                </div>
                                <div className='row_inputs flex'>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Salon",
                                            en: "Living room",
                                            sw: "Sebuleni"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="LivingRoom" onChange={(event) => { }} value={this.state.formValues.LivingRoom} />
                                    </div>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Salle  manger",
                                            en: "Dinner room",
                                            sw: "Chumba cha kulia"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="DinningRoom" onChange={(event) => { }} value={this.state.formValues.DinningRoom} />
                                    </div>
                                </div>
                                <div className='row_inputs flex'>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Douche",
                                            en: "Bathroom",
                                            sw: "Bafuni"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="BathRoom" onChange={(event) => { }} value={this.state.formValues.BathRoom} />
                                    </div>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Bureau",
                                            en: "Office",
                                            sw: "Ofisi"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="Office" onChange={(event) => { }} value={this.state.formValues.Office} />
                                    </div>
                                </div>

                                <div className='row_inputs flex'>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Parking",
                                            en: "Parking",
                                            sw: "Maegesho"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="Parking" onChange={(event) => { }} value={this.state.formValues.Parking} />
                                    </div>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Garage",
                                            en: "Garage",
                                            sw: "Gereji"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="Garage" onChange={(event) => { }} value={this.state.formValues.Garage} />
                                    </div>
                                </div>
                                <div className='row_inputs flex'>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Balcon",
                                            en: "Balcony",
                                            sw: "Balcony"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="Balcony" onChange={(event) => { }} value={this.state.formValues.Balcony} />
                                    </div>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Cuisine",
                                            en: "Kitchen",
                                            sw: "Jikoni"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <input name="Kitchen" onChange={(event) => { }} value={this.state.formValues.Kitchen} />
                                    </div>
                                </div>

                                <div className='row_inputs flex'>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Taille de la parcelle",
                                            en: "Plot size",
                                            sw: "Ukubwa wa kiwanja"
                                        }[`${this.props.currentLanguage}`]} (m)</h5>
                                        <input name="Size" onChange={(event) => { }} value={this.state.formValues.Size} />
                                    </div>
                                    <div className="update_input">
                                        <h5>{{
                                            fr: "Construit en",
                                            en: "Built in",
                                            sw: "Ime jengwa mwaka"
                                        }[`${this.props.currentLanguage}`]}</h5>
                                        <select onChange={(event) => { }} className="account_type" name="BuiltIn" value={this.state.formValues.BuiltIn}>
                                            <option value={0} disabled={true}>Built in</option>
                                            {Array.from({ length: new Date().getFullYear() - startYear }, (_, i) => {
                                                return new Date().getFullYear() - i;
                                            }).map((e, i) => {
                                                return <option key={i} value={Number(e)}>{e}</option>
                                            })}
                                        </select>
                                    </div>
                                </div>
                                <div className="update_input">
                                    <h5>{{
                                        fr: 'Equipement',
                                        en: 'Amenities',
                                        sw: 'Vitawishi'
                                    }[this.props.currentLanguage]}</h5>
                                    <MultiSelectInput isDisabled={isDisabled} intialValue={this.getSelectedAmenities(this.state.formValues)} placeholder="Amenities" items={amenities.map((e) => e.name[`${this.props.currentLanguage}`])} onConfirm={(selectedIndex) => {
                                        var amenitiesForDb = {};
                                        amenities.forEach((amenity, index) => {
                                            amenitiesForDb = { ...amenitiesForDb, [amenity.key]: [...selectedIndex].filter((e) => e === index).length > 0 };
                                        });
                                        this.setState(state => ({ ...state, formValues: { ...state.formValues, ...amenitiesForDb }, formValuesToSend: { ...state.formValuesToSend, ...amenitiesForDb } }));
                                    }} />
                                </div>
                                <div className="update_input">
                                    <h5>{{
                                        fr: 'Description',
                                        en: 'Description',
                                        sw: 'Maelezo'
                                    }[this.props.currentLanguage]}</h5>
                                    <textarea name='Description' onChange={(event) => { }} value={this.state.formValues.Description} />
                                </div>
                                <div className="divider" />
                            </form>
                        </fieldset>
                    </div>
                </div>
            </div>
            <div className='update_houses_screen_right'>
                <div style={{ height: '30rem', minHeight: '30rem', width: '100%' }}>
                    <Caroussel002
                        key={Math.random()}
                        max={15}
                        min={4}
                        hintText="Add images"
                        multiple={true}
                        extensions={['jpeg', 'jpg', 'png']}
                        initialValues={this.state.building.Images}
                        video={this.state.building.Video}
                        entityIdObject={{ BuildingId: this.state.building.id }}
                        builder={(index) => {
                            return Object.keys(this.state.building.Images[index] ?? {}).length ? <img alt='' src={this.state.building.Images[index]?.Url} style={{ height: '100%', width: '100%', objectFit: 'cover' }} /> : <></>
                        }}
                        uploadOptions={{
                            userUUID: this.props.userInformation.UID,
                            token: this.props.userInformation.accessToken,
                            field: housesImagesS3FolderNAme,
                            baseUrl: systemHousesBaseUrl,
                            uploadEndpoint: `building/add-image`,
                            uploadVideoEndpoint: `building/update/${this.state.building.id}`,
                            deleteEndpoint: (id) => `building/remove-image/${id}/${this.state.building.id}`,
                        }}
                        onUpload={(urlObject) => {
                            this.setState(state => ({ building: { ...state.building, Images: [urlObject, ...this.state.building.Images] } }));
                        }}
                        onDelete={(id) => {
                            this.setState(state => ({ building: { ...state.building, Images: state.building.Images.filter((e) => e.id !== id) } }));
                        }}
                        onVideoChanged={(url) => {
                            this.setState(state => ({ building: { ...state.building, Video: url } }));
                        }}
                    />
                </div>
                <div className="divider" /><div className="divider" />
                <div className='enable_house_inputs'>
                    <div className='enable_house_inputs_item'>
                        <h5>{{
                            fr: 'A Louer',
                            en: 'Rental',
                            sw: 'Kukodisha'
                        }[this.props.currentLanguage]}</h5>
                        <label className='switch'>
                            <input type='checkbox' checked={this.state.isForRent} value={this.state.isForRent} onChange={(event) => {
                                event.stopPropagation();
                                if (!event.target.checked) {
                                    if (this.state.building.Rental) {
                                        this.notForRent(this.state.building.Rental.id)
                                    } else {
                                        this.setState({ isForRent: event.target.checked });
                                    }
                                } else {
                                    this.setState({ isForRent: event.target.checked });
                                }
                            }} />
                            <span className='slider round' />
                        </label>
                    </div>
                    <div className='enable_house_inputs_item'>
                        <h5>{{
                            fr: 'A vendre',
                            en: 'For sale',
                            sw: 'Inauzwa'
                        }[this.props.currentLanguage]}</h5>
                        <label className='switch'>
                            <input type='checkbox' checked={this.state.isForSale} value={this.state.isForSale} onChange={(event) => {
                                event.stopPropagation();
                                if (!event.target.checked) {
                                    if (this.state.building.SaleDetail) {
                                        this.notForSale(this.state.building.SaleDetail.id)
                                    } else {
                                        this.setState({ isForSale: event.target.checked });
                                    }
                                } else {
                                    this.setState({ isForSale: event.target.checked });
                                }
                            }} />
                            <span className='slider round' />
                        </label>
                    </div>
                    <div className='enable_house_inputs_item'>
                        <h5>{{
                            fr: 'Visible publiquement ',
                            en: 'Publicly visible',
                            sw: 'Inaonekana hadharani'
                        }[this.props.currentLanguage]}</h5>
                        <label className='switch'>
                            <input type='checkbox' checked={this.state.isVisible} value={this.state.isVisible} onChange={(event) => {
                                event.stopPropagation();
                                this.makeVisible(event.target.checked);
                            }} />
                            <span className='slider round' />
                        </label>
                    </div>
                    {this.state.isAsyncCall && <i id='spinner_loader' className='fa-solid fa-spinner fa-spin' />}
                </div>
                <div className='divider' />
                <div className='divider' />
                {this.state.isForRent && <RentalForm terms={this.props.RentalTypes} buildingId={this.state.building.id} initialValue={this.state.building.Rental} onChange={(Rental) => {
                    this.setState(state => ({ building: { ...state.building, Rental: Rental } }));
                }} />}
                <div className='divider' />
                {this.state.isForSale && <SaleForm buildingId={this.state.building.id} initialValue={this.state.building.SaleDetail} onChange={(SaleDetail) => {
                    this.setState(state => ({ building: { ...state.building, SaleDetail: SaleDetail } }));
                }} />}
                <div className='divider' />
                <div className="update_input">
                    <h5>{{
                        fr: 'Chambre',
                        en: 'Bedroom',
                        sw: 'Chumba'
                    }[this.props.currentLanguage]}</h5>
                    <UpdateBedrooms houseId={this.state.building.id} rooms={this.state.building.BedRooms} currentLanguage={this.props.currentLanguage} />
                </div>
                <div className='divider' />

            </div>
        </div>

    }



    async utilsFetcher() {
        this.setState({ isAsyncCall: true });
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemHousesBaseUrl,
            endpoint: "api/utils",
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: localStorage.getItem(`${appName}act`) ?? ''
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const { BuildingType, RentalType } = response.response;
                this.props.dispatch(copyWith({ propertyTypes: BuildingType ?? [], RentalTypes: RentalType ?? [] }));
                this.setState({ isAsyncCall: false });
            } else {
                this.props.dispatch(copyWith({ propertyTypes: [], RentalTypes: [] }));
                this.setState({ isAsyncCall: false });
            }
        });
    };

    GetEntityObject() {
        this.setState({ isAsyncObjectCall: true });
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemHousesBaseUrl,
            endpoint: `api/building/my-own-building/${this.props.building.id}`,
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: this.props.userInformation?.accessToken
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const object = response.response;
                this.setState({
                    building: object,
                    formValues: this.getFormValues(object),
                    isAsyncObjectCall: false,
                    isForSale: object.SaleDetail ? true : false,
                    isForRent: object.Rental ? true : false,
                    isVisible: object.Actif ? true : false,
                });
            } else {
                this.setState({ isAsyncObjectCall: false, errorObjectMessage: getErrorMessage(response.error, 'en') });
            }
        });
    };

    notForSale(saleId) {
        if (!this.state.isAsyncCall) {
            this.setState({ isAsyncCall: true });
            const restApiServices = new ApiProvider();
            restApiServices.provider({
                baseUrl: systemHousesBaseUrl,
                endpoint: `api/building/delete-sale-detail/${saleId}`,
                apiMethod: ApiMethods.delete,
                successCodeExpectation: 200,
                requestData: {
                    token: {
                        accessToken: localStorage.getItem(`${appName}act`) ?? ''
                    },
                },
            }).then((response) => {
                if (typeof (response.error) === "undefined") {
                    this.setState({ isForSale: false, isAsyncCall: false });
                } else {
                    this.setState({ isAsyncCall: false, isDisabled: false, errorMessage: getErrorMessage(response.error, 'en') });
                }
            });
        }
    };

    notForRent(rentId) {
        if (!this.state.isAsyncCall) {
            this.setState({ isAsyncCall: true });
            const restApiServices = new ApiProvider();
            restApiServices.provider({
                baseUrl: systemHousesBaseUrl,
                endpoint: `api/building/delete-rental-detail/${rentId}`,
                apiMethod: ApiMethods.delete,
                successCodeExpectation: 200,
                requestData: {
                    token: {
                        accessToken: localStorage.getItem(`${appName}act`) ?? ''
                    },
                },
            }).then((response) => {
                if (typeof (response.error) === "undefined") {
                    this.setState({ isForRent: false, isAsyncCall: false });
                } else {
                    this.setState({ isAsyncCall: false, isDisabled: false, errorMessage: getErrorMessage(response.error, 'en') });
                }
            });
        }
    };

    makeVisible(state) {
        console.log(state);
        if (!this.state.isAsyncCall) {
            this.setState({ isAsyncCall: true });
            const restApiServices = new ApiProvider();
            restApiServices.provider({
                baseUrl: systemHousesBaseUrl,
                endpoint: `api/building/toggle-state/${this.state.building.id}/${state}`,
                apiMethod: ApiMethods.patch,
                successCodeExpectation: 200,
                requestData: {
                    token: {
                        accessToken: localStorage.getItem(`${appName}act`) ?? ''
                    },
                    body: {},
                },
            }).then((response) => {
                if (typeof (response.error) === "undefined") {
                    this.setState({ isVisible: !this.state.isVisible, isAsyncCall: false });
                } else {
                    this.setState({ isAsyncCall: false, errorMessage: getErrorMessage(response.error, 'en') });
                }
            });
        }
    };


    onUpdate(values) {
        if (!this.state.isAsyncCall) {
            this.setState({ isAsyncCall: true });
            const restApiServices = new ApiProvider();
            restApiServices.provider({
                baseUrl: systemHousesBaseUrl,
                endpoint: `api/building/update/${this.state.building.id}`,
                apiMethod: ApiMethods.patch,
                successCodeExpectation: 200,
                requestData: {
                    token: {
                        accessToken: localStorage.getItem(`${appName}act`) ?? ''
                    },
                    body: values,
                },
            }).then((response) => {
                if (typeof (response.error) === "undefined") {
                    this.setState({ formValuesToSend: {}, isAsyncCall: false });
                } else {
                    this.setState({ isAsyncCall: false, isDisabled: false, errorMessage: getErrorMessage(response.error, 'en') });
                }
            });
        }

    };
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage.code,
    userInformation: storeOf.madoshome.userInformation,
    propertyTypes: storeOf.madoshome.propertyTypes,
    RentalTypes: storeOf.madoshome.RentalTypes,
});

export default connect(mapStateToProps)(UpdateHouse);
