import { useSelector } from "react-redux";
// import react from 'react'

export default function Multilanguage({ComponentTorender, data, ...otherProps}){


    const CurrentLanguage = useSelector(state => state.madoshome.currentLanguage.code)

    return <ComponentTorender data={data[`${CurrentLanguage}`]} CurrentLanguage={CurrentLanguage} otherProps={otherProps}/>
}